import React, { Component } from 'react';
import {Tabs, Tab, Grid, Cell,} from 'react-mdl';
import ProjectsReactJs from './projectsreactjs';
import ProjectsPython from './projectspython';
import ProjectsCSharp from './projectscsharp';
import ProjectsAI from './projectsai';

class Projects extends Component {

    //constructor
    constructor(props){
        super(props)
        this.state = {activeTab : 0}
    }

    toggleCategories(){
        if(this.state.activeTab === 0){
            return(
                <ProjectsReactJs>
                </ProjectsReactJs>
            )
        }
        else if(this.state.activeTab === 1){

            return(
                <ProjectsPython>
                </ProjectsPython>
                // temp solution, will had better will do to this
            )
        }else if(this.state.activeTab === 2){
            return(
                <ProjectsAI>
                </ProjectsAI>
            )
        }
        else if(this.state.activeTab === 3){
            return(
                <ProjectsCSharp>
                </ProjectsCSharp>
            )
        }
    }

    
    
    render() { 
        return (
          <div>
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                    <Tab>React</Tab>
                    <Tab>Python</Tab>
                    <Tab>AI</Tab>
                    <Tab>C#</Tab>
                </Tabs>

                <Grid>
                    <Cell col={12}>
                        <div className="content">{this.toggleCategories()}</div>
                    </Cell>
                </Grid>
          </div>  
          );
    }
}
 
export default Projects;
