import React, { Component } from 'react';
import {Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton, Redirect} from 'react-mdl';


class ProjectsCSharp extends Component {

    //constructor
    constructor(props){
        super(props)
        this.state = {activeTab : 0}
        this.projectname = "Python"
    }

  
    render() { 
        return (
 
            <div className="projects-grid">
                {/* Project 1*/}
                <Card shadow={5} style={{minWidth:'450', margin: 'auto'}}>
                    <CardTitle style={{color: 'white', height: '176px', background:
                    'url(data:image/jpeg;base64,) center / cover'}}>
                    Wah Lee Ecommerce
                    </CardTitle>
                    <CardText>
                    In this project, I been in charge of dealing with backend business logic, create API and do a bit frontend modify, this project is using gradenode as base which is a Open Source NoSQL CMS eCommerce platform.
                    </CardText>
                    
                    <CardActions border>
                        <Button colered disabled>GitHub</Button>
                        <Button colored disabled>CodePen</Button>
                        <Button href="https://staging.wahleegroup.com.my/" target="_blank" colored>LiveDemo</Button>
                    </CardActions>

                    <CardMenu style={{color: '#fff'}}>
                        <IconButton name="shared" />
                    </CardMenu>
                </Card>

            </div>
        );
    }
}
 
export default ProjectsCSharp;
